//state 
const initState = {
    num: "",
    content: "",
};

//
export const ADD_NUM = "ADD_NUM";

const reducer = (state = initState, action) => {
    switch (action.type) {
        case ADD_NUM:
            return {
                ...state, //  
                num: action.data, //  state
            };
        default:
            return state;
    }
};

export default reducer;
